  .header {
    border-bottom: solid 1px #D9D9D9;
    position: relative;
    padding: 16px 0px;
  }
  
  .header .nav {
    z-index: 9;
  }
  
  .header .logo {
    margin-right: 53px;
  }
  
  .header .nav li {
    position: relative;
    display: inline-block;
    padding: 0px 16px;
  }
  
  .header .nav li.dropdown {
    position: relative;
    overflow: initial;
  }
  
  .header .nav li .dropdown-menu li a {
    width: 100%;
  }
  
  .header .nav li a {
    color: #000;
    overflow: hidden;
  }
  
  .header .nav li a:hover {
    color: #E30613;
  }
  
  /* .header .nav li {
    overflow: hidden;
  } */
  
  .header .nav li a {
    display: inline-block;
    position: relative;
  }
  
  .header .nav li a::after,
  .header .nav li a::before {
    position: absolute;
    bottom: 0px;
    content: "";
    height: 3px;
    transition: all .5s ease-in-out;
    width: 0;
    left: -100%;
    background-color: #E30613;
  }
  
  .header .nav li a::before {
    right: -100%;
    left: auto;
  }
  
  .header .nav li a:hover::after {
    left: 0px;
  }
  
  .header .nav li a:hover::before {
    right: 0px;
  }
  
  .header .nav li .dropdown-menu {
    position: absolute;
    left: 0;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 0px 0px;
    z-index: 999;
    min-width: 130px;
    opacity: 0;
    visibility: hidden;
  }
  
  .header .nav li .dropdown-menu li {
    display: block;
    padding: 8px 0px;
    text-align: center;
  }
  
  .header .nav li .dropdown-menu li:hover {
    background-color: #E30613;
  }
  
  .header .nav li .dropdown-menu li:hover a {
    color: #fff;
  }
  
  .header .nav li:hover .dropdown-menu {
    opacity: 1;
    visibility: visible;
  }
  
